body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}




text {
  pointer-events: none;
}

rect {
  cursor: pointer;
}



.y-axis text {
  font-weight: 600;
  color: #fff !important;
}

.y-axis .domain {
  stroke: #467F63
}

/* TOOLTIP DESIGN  */

.tooltip {
  position: absolute;
  text-align: left;
  width: auto;
  height: 0;
  color: #333;
  padding: 6px 12px;
  background: #fff;
  border: 1px solid #333;
  border-radius: 8px;
  line-height: 0px;
  pointer-events: none;
  box-shadow: 0px 1px 5px 1px rgba(51, 51, 51, 0.2);
  opacity: 0;
}

hr {
  display: block;
  margin-top: .3rem;
  margin-bottom: .3rem;
  margin-left: auto;
  margin-right: auto;
  border: none;
  height: 1px;
  background-color: #333;
}

.bold {
  font-weight: 800;
}

.tooltip text {
  line-height: 20px;
}



h1 {
  font-size: 3rem;
  font-weight: 800;
  line-height: 50px;
  letter-spacing: 3px;
  word-spacing: 5px;
  margin-top: 10px !important;
  pointer-events: none;
}



* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #333 !important;
}


.visualization {
  position: relative;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

.chart-one {
  position: absolute;
  display: inline-block;
  left: 0;
  top: 0;
  width: 50%;
  height: 100%;
}

.chart-two {
  position: absolute;
  display: inline-block;
  left: 50%;
  top: 0;
  width: 50%;
  height: 100%;
}

.text-controls {
  position: absolute;
  display: inline-block;
  left: 100px;
  bottom: 40px;
  width: 30%;
  height: auto;
  z-index: 99;
}

.credit{
  position: absolute;
  bottom: 0;
  left: 100px

}

.credit a {
    font-size: 8px !important;
}

